(function() {
  var slice = [].slice;

  app.behaviors.debug = {
    debugInfo: function() {
      var args, name;
      args = 1 <= arguments.length ? slice.call(arguments, 0) : [];
      name = ('🏻 ' + this.nodeName).toLowerCase();
      args.unshift(name);
      return lib.debug.apply(null, args);
    },
    debug: function() {
      var args, name;
      args = 1 <= arguments.length ? slice.call(arguments, 0) : [];
      name = ('🏿 ' + this.nodeName).toLowerCase();
      args.unshift(name);
      lib.debug.apply(null, args);
      return console.log('hello');
    }
  };

}).call(this);
